<template>
  <v-container>
    <div>
      <v-avatar size="100" tile>
        <v-img src="@/assets/pic/baby_face.png" alt :aspect-ratio="1/1" />
      </v-avatar>
      <div style="display:inline-block">
        <span class="text-h6">{{ this.name }}同學</span>
        <span class="text-h6 ml-4">{{ this.monthAge | monthAgeDisplay }}</span>
      </div>
    </div>
    <v-timeline dense>
      <template v-for="(milestone,index) in milestones">
        <v-timeline-item
          :key="index"
          :icon="milestone.icon"
          :color="milestone.color"
          @click.native="onTapDot(milestone)"
        >
          <v-card>
            <v-card-title>
              {{ milestone.time }}
              <br />
              {{ calcDayAge(milestone.time) | dayToMonthAge | monthAgeDisplay }}
              <br />
            </v-card-title>
            <v-card-text>
              <v-img src="@/assets/pic/baby.png"></v-img>每月紀錄
            </v-card-text>
          </v-card>
        </v-timeline-item>
        <v-slide-x-reverse-transition group :key="'s'+index">
          <template v-if="milestone.expand">
            <v-timeline-item
              dense
              v-for="(item,subindex) in milestone.subline"
              :key="subindex"
              small
              fill-dot
            >
              <v-alert :icon="item.icon">
                <span class="text-h6">{{item.time}}</span>
                <v-list dense>
                  <v-list-item v-for="(i,index) in item.scales" :key="index">{{ i }}</v-list-item>
                </v-list>
              </v-alert>
            </v-timeline-item>
          </template>
        </v-slide-x-reverse-transition>
      </template>
    </v-timeline>
    <span class="text-h4 font-weight-bold ml-8">繼續前進~~~~~</span>
    <v-bottom-sheet v-model="sheet" inset>
      <v-sheet class="text-center">
        <div class="text-h5">修改寶寶生日</div>
        <v-date-picker v-model="birthDay"></v-date-picker>
      </v-sheet>
    </v-bottom-sheet>
  </v-container>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      name: "三毛",
      birthDay: "2019-02-02",
      sheet: false,

      milestones: [
        {
          time: "2019 10/10",
          name: "加入小熊",
          detail: "歡迎加入大家庭!",
        },
        {
          time: "2019 11/01",
          name: "",
          detail: "達到 5 個能力指標 經驗:30",
          expand: false,
        },
        {
          time: "2019 12/01",
          name: "",
          detail: "達到 15 個能力指標 經驗:60",
          expand: false,
        },
        {
          time: "2020 01/01",
          name: "",
          detail: "達到 5 個能力指標 經驗:30",
          expand: false,
        },
      ],
    };
  },
  methods: {
    editDays() {
      this.sheet = true;
    },
    calcDayAge(date) {
      return (new Date(date) - this.birthDayDate) / 86400000;
    },
    onTapDot(val) {
      val.expand = !val.expand;
    },
  },
  filters: {
    dayToMonthAge(val) {
      return Math.floor(val / 30);
    },
    monthAgeDisplay(month) {
      return `月齡: ${month} 個月`;
    },
  },
  computed: {
    birthDayDate() {
      return new Date(this.birthDay);
    },
    dayAge() {
      return (new Date() - this.birthDayDate) / 86400000;
    },
    monthAge() {
      return Math.floor(this.dayAge / 30);
    },
  },
};
</script>

